/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  background-color: #fff;
  color: #90278e;
  cursor: pointer;
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 14px;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  padding: 18px 0;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active,
.accordion:hover {
  background-color: rgba(144, 39, 142, 0.1);
  /* box-shadow: 0px 8px 22px -12px; */
}

/* Style the accordion panel. Note: hidden by default */
.panel {
  padding: 0x;
  background-color: white;
  display: none;
  overflow: hidden;
}

.active {
  display: block !important;
}

.icon-closed {
  transform: rotate(0deg);
  transition: transform 0.3s;
}

.icon-opened {
  transform: rotate(180deg);
  transition: transform 0.3s;
}

.border-bottom {
  border-bottom: 1px dashed #72008d;
}

.accodion-container {
  margin-bottom: 32px;
}

.accordion-content {
  padding-bottom: 32px;
}

.accordion-description {
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: 0.24px;
  text-align: left;
  color: #000000;
  padding: 0;
  margin: 0;
  margin-top: 16px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: -webkit-fill-available;
}
