@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
/**
* Make the field a flex-container, reverse the order so label is on top.
*/

.icon-container {
  margin: 0px;
  padding: 0px;
  align-self: flex-end;
  color: #bc79cc;
}

.content {
  width: 100%;
  /* margin-bottom: 18px; */
}

.field-group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-bottom: 38px; */
  border-bottom: 1px solid #72008d;
  flex: 1 1;
  /* max-width: 360px; */
}

.field {
  display: flex;
  flex-flow: column-reverse;
  width: 100%;
}
/**
* Add a transition to the label and input.
* I'm not even sure that touch-action: manipulation works on
* inputs, but hey, it's new and cool and could remove the
* pesky delay.
*/
label,
.input {
  transition: all 0.2s;
  touch-action: manipulation;
  resize: none;
}

.input {
  font-size: 14px;
  border: 0;
  /* border-bottom: 1px solid #72008d; */
  font-family: inherit;
  -webkit-appearance: none;
  border-radius: 0;
  padding: 0;
  cursor: text;
  padding-bottom: 6px;
  font-family: Poppins;
  color: #bc79cc;
  line-height: 21px;
  max-width: 100%;
  min-width: 100%;
  width: 100%;
}

.input:focus {
  outline: 0;
  /* border-bottom: 2px solid #72008d;*/
  color: #72008d;
}

label {
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #bc79cc;
  font-family: Poppins;
  font-weight: bold;
  font-size: 12px;
  opacity: 0;
  margin-bottom: 9px;
  line-height: 18px;
}
/**
* Translate down and scale the label up to cover the placeholder,
* when following an input (with placeholder-shown support).
* Also make sure the label is only on one row, at max 2/3rds of the
* fieldâ€”to make sure it scales properly and doesn't wrap.
*/
.input:placeholder-shown + label {
  cursor: text;
  max-width: 66.66%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
  -webkit-transform: translate(0, 2.125rem) scale(1.5);
          transform: translate(0, 2.125rem) scale(1.5);
}
/**
* By default, the placeholder should be transparent. Also, it should
* inherit the transition.
*/
::-webkit-input-placeholder {
  opacity: 1;
  -webkit-transition: inherit;
  transition: inherit;
}
/**
* Show the placeholder when the input is focused.
*/
.input:focus::-webkit-input-placeholder {
  opacity: 0;
}

/**
* When the element is focused, remove the label transform.
* Also, do this when the placeholder is _not_ shown, i.e. when
* there's something in the input at all.
*/
.input:not(:placeholder-shown) + label,
.input:focus + label {
  -webkit-transform: translate(0, 0) scale(1);
          transform: translate(0, 0) scale(1);
  cursor: pointer;
  opacity: 1;
}

.input:focus + label {
  -webkit-transform: translate(0, 0) scale(1);
          transform: translate(0, 0) scale(1);
  cursor: pointer;

  opacity: 1;
  color: #72008d !important;
}

.input:disabled {
  cursor: default;
  background-color: #ffff;
}

input:disabled + label {
  cursor: default;
}

.content:focus-within .icon-container {
  color: #72008d !important;
}

.content:focus-within .field-group {
  border-bottom: 2px solid #72008d;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #bc79cc;
  opacity: 0.8; /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #bc79cc;
  opacity: 0.8; /* Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #bc79cc;
  opacity: 0.8; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #bc79cc;
  opacity: 0.8; /* Firefox */
}

.border-bottom-none {
  border-bottom: 0;
}

.dropdown {
  position: relative;
  display: inline-block;

  /* border-bottom: 1px solid #72008d; */
  color: #bc79cc;
  font-size: 14px;
  font-family: 'Poppins';
  font-weight: 400;
  letter-spacing: 0px;
  width: -webkit-fill-available;
  padding-bottom: 5px;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  width: 360px;
  top: 55px;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-option:active .dropdown-option {
  display: none;
}

.item-active-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.icon-drop-down {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.dropdown:hover .icon-drop-down {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  background-color: #fff;
  color: #90278e;
  cursor: pointer;
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 14px;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  padding: 18px 0;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active,
.accordion:hover {
  background-color: rgba(144, 39, 142, 0.1);
  /* box-shadow: 0px 8px 22px -12px; */
}

/* Style the accordion panel. Note: hidden by default */
.panel {
  padding: 0x;
  background-color: white;
  display: none;
  overflow: hidden;
}

.active {
  display: block !important;
}

.icon-closed {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.icon-opened {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.border-bottom {
  border-bottom: 1px dashed #72008d;
}

.accodion-container {
  margin-bottom: 32px;
}

.accordion-content {
  padding-bottom: 32px;
}

.accordion-description {
  font-family: 'Poppins';
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: 0.24px;
  text-align: left;
  color: #000000;
  padding: 0;
  margin: 0;
  margin-top: 16px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: -webkit-fill-available;
}

html {
  height: 100%;
  width: 100%;
  font-family: 'Poppins';
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  background: rgb(144, 39, 142);
  background: linear-gradient(
    180deg,
    rgba(144, 39, 142, 1) 0%,
    rgba(114, 0, 141, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#90278e",endColorstr="#72008d",GradientType=1);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(144, 39, 142, 1);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.7);

  /* background: rgba(144, 39, 142, 0.3); */
}

p {
  margin: 0;
}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

