@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap');

html {
  height: 100%;
  width: 100%;
  font-family: 'Poppins';
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  background: rgb(144, 39, 142);
  background: -moz-linear-gradient(
    180deg,
    rgba(144, 39, 142, 1) 0%,
    rgba(114, 0, 141, 1) 100%
  );
  background: -webkit-linear-gradient(
    180deg,
    rgba(144, 39, 142, 1) 0%,
    rgba(114, 0, 141, 1) 100%
  );
  background: linear-gradient(
    180deg,
    rgba(144, 39, 142, 1) 0%,
    rgba(114, 0, 141, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#90278e",endColorstr="#72008d",GradientType=1);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(144, 39, 142, 1);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.7);

  /* background: rgba(144, 39, 142, 0.3); */
}

p {
  margin: 0;
}

a {
  text-decoration-line: none;
}
